import React from 'react'
import Layout from '../Components/Layout/Layout'
import { Box, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography } from '@mui/material'
import SupportAgentIcon from '@mui/icons-material/SupportAgent';
import MailIcon from '@mui/icons-material/Mail';
import HomeIcon from '@mui/icons-material/Home';


const Contact = () => {
  return (
    <Layout>
       <Box sx={{my:5, ml:10, "& h4":{ fontWeight: 'bold', mb: 2}}}>
        <Typography variant='h5'>
          Contact my Business
        </Typography>
        <p>This is the best Business in the city/town. Feel free to contact us via our office address, contact us via phone, contact us via email-address. </p>
        <br />
        <p>This is the best Business in the city/town. Feel free to contact us via our office address, contact us via phone, contact us via email-address. </p>
       </Box>
       <Box sx={{ m: 3, width: "600px", ml: 10, "@media (max-width:600px)":{
        width: "300px",
       } }}>
        <TableContainer component={Paper}>
           <Table aria-label="contact-label">
            <TableHead>
              <TableRow>
                <TableCell sx={{bgcolor: 'black', color: 'white'}} align='center'>
                  Contact Details
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              <TableRow>
                <TableCell>
                   <SupportAgentIcon sx={{color:'red', pt: 1}}/> +XX-XXXXXXXXXXX (Business Contact Number)
                </TableCell>
              </TableRow>
              <TableRow>
              <TableCell>
                   <MailIcon sx={{color:'skyblue', pt: 1}}/> youremail@yourbusinessemail.com
                </TableCell>
              </TableRow>
              <TableRow>
              <TableCell>
                   <HomeIcon sx={{color:'purple', pt: 1}}/> Plot No., Shop No., Landmark, City, State, Country, Pin Code 
                </TableCell>
              </TableRow>
            </TableBody>
           </Table>
        </TableContainer>
       </Box>
    </Layout>
  )
}

export default Contact